.aboutContainer {
  height: auto;
  width: auto;
  border: 0px solid red;
  display: grid;
  box-sizing: border-box;
  padding: 70px;
  grid-template-columns: 40% 60%;
  align-items: center;
}
body {
  background-color: #090514;
}
.codeGif {
  width: 70%;
  border-radius: 20px;
  border: 2px solid #dd9a05;
}
.heading {
  text-align: center;
  margin: auto;
  color: white;
  font-size: 30px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-top: 50px;
  margin-bottom: 6px;
}
.line {
  width: 200px;
  margin: auto;
  /* margin-bottom: 15px; */
  border: 2px solid #dd9a05;
}

.para {
  text-align: left;
  /* width: 50%; */
  font-size: 18px;
  margin: auto;
  color: white;
  margin-bottom: 25px;
  letter-spacing: 1px;
}
/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 1024px) {
  .aboutContainer {
    /* background-color: teal; */
    grid-template-columns: repeat(2, 1fr);
  }
  .codeGif {
    width: 75%;
  }
  .para {
    font-size: 17px;
  }
}

/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {
  .aboutContainer {
    background-color: black;
    grid-template-columns: repeat(2, 1fr);
    padding: 50px;
    padding-top: 20px;
  }
  .codeGif {
    width: 80%;
    border: 1px solid #dd9a05;
  }
  .paraContainer {
    border: 0px solid #dd9a05;
    margin: auto;
    margin-right: -50px;
  }
  .para {
    font-size: 15px;
    width: 90%;
    margin-bottom: 15px;
  }
}
/* mobile */
/* Media Query for Mobile Devices */
@media (max-width: 480px) {
  .aboutContainer {
    /* background-color: maroon; */
    border: 0px solid blue;
    grid-template-columns: repeat(1, 1fr);
    padding: 10px;
  }
  .heading {
    margin-top: 60px;
  }
  .line {
    margin-bottom: 60px;
  }
  .codeGif {
    margin: auto;
    width: 85%;
    margin-bottom: 50px;
  }

  .para {
    width: 90%;
    font-size: 16px;
    margin-bottom: 19px;
    text-align: justify;
  }
}
