.homeContainer {
  height: 500px;
  width: auto;
  background-color: #090514;
  display: grid;
  grid-template-columns: 60% 40%;
  align-items: center;
}
.headingContainer {
  height: auto;
  width: auto;
  border: 0px solid black;
  margin: auto;
  margin-left: 15%;
}
.headingContainer p {
  color: white;
  font-weight: bold;
  font-size: 15px;
  font-family: Arial, Helvetica, sans-serif;
}
.headingContainer h2 {
  color: white;
  font-size: 40px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  letter-spacing: 1.5px;
  margin-bottom: -10px;
  margin-top: -10px;
}
.headingContainer h1 {
  color: #dd9a05;
  font-size: 42px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  letter-spacing: 1.5px;
}
.resumeBtn {
  border: none;
  color: white;
  background-color: #dd9a05;
  border: 1px solid #dd9a05;
  border-radius: 7px;
  font-size: 16px;
  padding: 10px 14px;
}
.resumeBtn:hover {
  border: none;
  color: #dd9a05;
  background-color: #090514;
  border: 3px solid #dd9a05;
  cursor: pointer;
  /* padding: 11px 15px; */
}
.profileContainer {
  height: auto;
  width: auto;
  border: 0px solid red;
  margin: auto;
}
.profilePic {
  width: 300px;
  border-radius: 100%;
}

/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 1024px) {
  .profilePic {
    width: 280px;
  }
}

/* Media Query for low resolution  Tablets, Ipads */
@media (min-width: 481px) and (max-width: 767px) {
  .profilePic {
    width: 200px;
  }
  .headingContainer h2 {
    font-size: 32px;
    letter-spacing: 1px;
    margin-bottom: -10px;
    margin-top: -10px;
  }
  .headingContainer h1 {
    font-size: 40px;
    letter-spacing: 1.2px;
    width: auto;
  }
}
/* mobile */
/* Media Query for Mobile Devices */
@media (max-width: 480px) {
  .homeContainer {
    height: 500px;
    width: auto;
    background-color: #090514;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
  }
  .headingContainer {
    margin: auto;
    box-sizing: border-box;
    padding: 20px;
  }
  .headingContainer p {
    text-align: center;
  }
  .headingContainer h2 {
    text-align: center;
    font-size: 30px;
  }
  .headingContainer h1 {
    text-align: center;
    font-size: 32px;
    word-spacing: -2px;
    letter-spacing: 0.5px;
  }
  .resumeBtn {
    display: block;
    margin: auto;
  }
  .profilePic {
    width: 180px;
  }
  /* body {
    background-color: yellow;
  } */
}
