.nav {
  height: 50px;
  width: auto;
  border: 1px solid black;
  background-color: #dd9a05;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
  position: sticky;
  top: 0;
}
.nav div {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.nav div li {
  list-style-type: none;
  padding: 0px 10px 0px 15px;
  color: white;
}
.nav div li:hover {
  cursor: pointer;
}
